<template>
  <div class="leaderboard">
    <div class="filter">
      <div class="top-space">
        国家：
        <el-select v-model="countryCode">
          <el-option
            v-for="item in countries"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      &nbsp; &nbsp;
      <div class="top-space">
        模式：
        <el-select v-model="category">
          <el-option
            v-for="category in categories"
            :key="category"
            :label="category"
            :value="category"
          >
          </el-option>
        </el-select>
      </div>
      &nbsp; &nbsp;
      <div class="top-space">
        游戏平台：
        <el-select v-model="platform">
          <el-option
            v-for="item in platforms"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      &nbsp; &nbsp;
      <div class="top-space">
        仅显示玩家不同平台最好成绩&nbsp;
        <el-switch v-model="onlyShowBest"> </el-switch>
      </div>
      &nbsp; &nbsp;
    </div>
    <el-divider> </el-divider>
    <el-table
      class="table"
      stripe
      v-loading="loading"
      :data="filteredSpeedruns"
      width="100%"
    >
      <el-table-column label="排名" min-width="50px">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column label="玩家" min-width="235px">
        <template slot-scope="scope">
          <el-tooltip
            v-if="scope.row.Country != '无'"
            :content="scope.row.Country"
            placement="top"
          >
            <el-image
              class="flags"
              :src="`https://www.speedrun.com/images/flags/${scope.row.CountryCode}.png`"
              lazy
            >
            </el-image>
          </el-tooltip>
          <el-link class="player" :href="scope.row.PlayerURL" target="_blank">{{
            scope.row.Player
          }}</el-link>
        </template>
      </el-table-column>
      <el-table-column
        prop="Platform"
        label="游戏平台"
        align="center"
        min-width="80"
      >
      </el-table-column>
      <el-table-column prop="Time" label="时间" min-width="150px">
      </el-table-column>
      <el-table-column label="视频地址" min-width="80px">
        <template slot-scope="scope">
          <el-link :href="scope.row.VideoURL" target="_blank">{{
            scope.row.VideoPlatform
          }}</el-link>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      speedruns: [],
      countries: [
        {
          value: "不限",
          label: "不限",
        },
        {
          value: "cn",
          label: "中国",
        },
      ],
      countryCode: "cn",
      platforms: [
        {
          value: "不限",
          label: "不限",
        },
        {
          value: "PC",
          label: "PC",
        },
        {
          value: "Android",
          label: "安卓",
        },
        {
          value: "iOS",
          label: "iOS",
        },
      ],
      platform: "不限",
      categories: ["Glitchless", "Snake"],
      category: "Glitchless",
      onlyShowBest: true,
      loading: true,
    };
  },
  computed: {
    filteredSpeedruns() {
      return this.speedruns.filter(
        (speedrun) =>
          (this.countryCode === "不限" ||
            speedrun.CountryCode === this.countryCode) &&
          (this.platform === "不限" || speedrun.Platform === this.platform) &&
          (this.category === "不限" || speedrun.Category === this.category) &&
          (!this.onlyShowBest || this.platform != "不限" || speedrun.Fastest)
      );
    },
  },
  mounted() {
    this.getSpeedruns();
  },
  methods: {
    async getSpeedruns() {
      const headers = new Headers({
        "X-LC-Id": "3Dec7Zyj4zLNDU0XukGcAYEk-gzGzoHsz",
        "X-LC-Key": "uHF3AdKD4i3RqZB7w1APiFRF",
        "Content-Type": "application/json",
      });
      const options = {
        method: "GET",
        headers: headers,
      };
      let params = {
        order: "TotalTime",
        keys: "Player,Category,UID,TotalTime,Platform,Time,VideoPlatform,VID,Country,CountryCode,Fastest,-createdAt,-updatedAt,-objectId",
        limit: 1000,
      };
      const url = `https://3dec7zyj.lc-cn-n1-shared.com/1.1/classes/Speedrun?${new URLSearchParams(
        params
      )}`;
      const response = await fetch(url, options);
      this.speedruns = (await response.json()).results;
      this.speedruns.forEach((speedrun) => {
        switch (speedrun.VideoPlatform) {
          case "哔哩哔哩":
            speedrun.VideoURL = `https://www.bilibili.com/video/${speedrun.VID}`;
            speedrun.PlayerURL = `https://space.bilibili.com/${speedrun.UID}`;
            break;
          case "YouTube":
            speedrun.VideoURL = `https://www.youtube.com/watch?v=${speedrun.VID}`;
            speedrun.PlayerURL = `https://www.youtube.com/channel/${speedrun.UID}`;
            break;
          case "Twitch":
            speedrun.VideoURL = `https://www.twitch.tv/videos/${speedrun.VID}`;
            speedrun.PlayerURL = `https://www.twitch.tv/${speedrun.UID}`;
            break;
          default:
            break;
        }
        if (
          !this.countries.some(
            (country) => country.value === speedrun.CountryCode
          ) &&
          speedrun.CountryCode !== "无"
        ) {
          this.countries.push({
            value: speedrun.CountryCode,
            label: speedrun.Country,
          });
        }
      });
      this.loading = false;
    },
  },
};
</script>

<style scoped>
.table {
  width: 100%;
}
.filter {
  display: flex;
  flex-wrap: wrap;
  place-items: center;
  justify-content: center;
}
.flags {
  width: 18px;
  display: inline-flex;
  justify-content: center;
  text-align: center;
  margin: 5px;
}
.player {
  display: inline-block;
}
.top-space {
  margin-top: 20px;
}
</style>